<template>
  <div class="bg-white rounded-lg p-4 space-y-3">
    <h1>진행 상황</h1>
    <div class="flex justify-center space-x-3">
      <div>할일: {{ $store.getters.progress.todo }}</div>
      <div>진행중: {{ $store.getters.progress.doing }}</div>
      <div>완료: {{ $store.getters.progress.done }}</div>
      <div>진행율: ({{ $store.getters.progress.text }})</div>
    </div>
    <div v-if="loading">작업중입니다.</div>
    <div v-else-if="errorMessage.length > 0"></div>
    <div v-else class="space-y-6">
      <div v-if="ListTodo.length > 0" class="flex justify-center space-x-6">
        <div class="w-32 font-bold text-lg">할 일</div>
        <div class="w-64 text-left">
          <div v-for="data in ListTodo" :key="data.rid">
            {{ data.rid }}. {{ data.contents }}
          </div>
        </div>
      </div>

      <div v-if="ListDoing.length > 0" class="flex justify-center space-x-6">
        <div class="w-32 font-bold text-lg">진행중</div>
        <div class="w-64 text-left">
          <div v-for="data in ListDoing" :key="data.rid">
            {{ data.rid }}. {{ data.contents }}
          </div>
        </div>
      </div>

      <div v-if="ListDone.length > 0" class="flex justify-center space-x-6">
        <div class="w-32 font-bold text-lg">완료</div>
        <div class="w-64 text-left">
          <div v-for="data in ListDone" :key="data.rid">
            {{ data.rid }}. {{ data.contents }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: true,
      errorMessage: "",
      listTodo: [],
      listDoing: [],
      listDone: [],
    };
  },
  async mounted() {
    const reqWeb = {
      api: "getProgress",
      // loginToken: localStorage.getItem("loginToken"),
      // userUid: this.$store.getters.userUid,
    };

    this.loading = true;
    const res = await this.$store.callAsync(reqWeb);
    this.loading = false;

    if (res.code !== 100) {
      this.errorMessage = res.message;
      return;
    }

    this.listTodo = res.list_todo;
    this.listDoing = res.list_doing;
    this.listDone = res.list_done;
  },
  computed: {
    ListTodo() {
      return this.listTodo;
    },
    ListDoing() {
      return this.listDoing;
    },
    ListDone() {
      return this.listDone;
    },
  },
};
</script>
